var html = require('choo/html')

var TITLE = 'garden-frontend - main'

module.exports = view

const tagEl = (tags) => html`<span>${tags.map((tag) => html`<span class="bg-light-gray ma1">${tag}</span>`)}`
const entry = (link) => html`<div class="mv2"><a href="${link.Link}" target="_blank">${link.Title} </a> ${parseMarkdown(link.Description)} ${tagEl(link.Tags)}</div>`

function view (state, emit) {
  if (state.title !== TITLE) emit(state.events.DOMTITLECHANGE, TITLE)

  return html`
    <body class="code lh-copy">
      <main class="pa3 cf center">
       <h1>hydra garden of links</h1>
       <div>
        ${state.links.map(entry)}
      </main>
    </body>
  `

  function handleClick () {
    emit('clicks:add', 1)
  }
}

function parseMarkdown(markdownText) {
	const htmlText = markdownText
		.replace(/^### (.*$)/gim, '<h3>$1</h3>')
		.replace(/^## (.*$)/gim, '<h2>$1</h2>')
		.replace(/^# (.*$)/gim, '<h1>$1</h1>')
		.replace(/^\> (.*$)/gim, '<blockquote>$1</blockquote>')
		.replace(/\*\*(.*)\*\*/gim, '<b>$1</b>')
		.replace(/\*(.*)\*/gim, '<i>$1</i>')
		.replace(/!\[(.*?)\]\((.*?)\)/gim, "<img alt='$1' src='$2' />")
		.replace(/\[(.*?)\]\((.*?)\)/gim, "<a href='$2'>$1</a>")
	//	.replace(/\n$/gim, '<br />')

	const span =  html`<span class="f6"></span>`
  span.innerHTML = htmlText.trim()
  return span
}